	import uniPopup from '@/components/uni-popup/uni-popup.vue'
	import {
		mapGetters,
		mapMutations
	} from "vuex"
	export default {
		components: {
			uniPopup
		},
		data() {
			return {
				maskState: 0, //优惠券面板显示状态
				desc: '', //备注
				maskFlag: false,
				payType: 1, //1微信 2支付宝
				timer: null,
				hour: 0,
				minute: 0,
				second: 0,
				day:0,
				orderState: true, //订单状态
				// couponList: [{
				// 		title: '新用户专享优惠券',
				// 		price: 5,
				// 	},
				// 	{
				// 		title: '庆五一发一波优惠券',
				// 		price: 10,
				// 	},
				// 	{
				// 		title: '优惠券优惠券优惠券优惠券',
				// 		price: 15,
				// 	}
				// ],
			}
		},
		computed: {
			...mapGetters({
				getOrderData: 'getOrderData',
				getAddressData: 'getAddressData',
				getOrederState: 'getOrederState',
			})
		},
		onLoad(option) {
			//商品数据
			//let data = JSON.parse(option.data);
			//console.log(data);
			// 获取订单信息
			this.getDefaultAddress();
			if (this.getOrderData.status == 1 || this.getOrderData.status == 3) {
				this.countDown(this.getOrderData.countdown_at);
			}
		},
		onUnload() {
			console.log("卸载页面");
			clearInterval(this.timer);
		},
		methods: {
			...mapMutations({
				setAddressState: 'setAddressState',
				setAddressData: 'setAddressData',
				setReturnOrder: 'setReturnOrder',
				setOrderData:'setOrderData',
				setOrderState:'setOrderState'
			}),
            //联系商家
            concat(){
                uni.makePhoneCall({
                    phoneNumber: this.getOrderData.factory[0].factory_phone //仅为示例
                });
			},
            // 粘贴板
            copye(data) {
                uni.setClipboardData({
                    data: data,
                    success: function() {
                        console.log('success');
                        uni.showToast({
                            title: '复制成功',
                            duration: 1000,
                            icon: "none"
                        });
                    }
                });
            },
			// 确认收货
			confirmReceipt(){
				this.$api.orderFinish({
					order_id:this.getOrderData.order_id
				}).then(res=>{
					console.log(res);
					uni.showToast({
						title: res.data.msg,
						duration: 1000,
						icon: "none"
					});
					if(res.data.errno == 0){
						this.toOrderDetail({
							order_id:this.getOrderData.order_id
						})
					}
				})
			},
			
			toOrderDetail(item){
				this.$api.orderDetail({order_id:item.order_id}).then(res=>{
					if(res.data.errno == 0){
						console.log(res);
						let data = res.data.data;
						// 设置vuex的收货地址信息
						this.setAddressData({
							address:data.address,
							address_id:"",
							name:data.name,
							phone:data.phone
						})
						this.setOrderData(data);//vuex设置订单信息
						this.setOrderState(data.status);//vuex设置订单状态
					}else{
						
					}
				})
			},
			
			// 倒计时
			countDown(time) {
				let startTime = time * 1000; //后端给的时间
				let nowTime = new Date().getTime(); //现在的时间
				let spareTime = (startTime - nowTime) / 1000;
				this.timer = null; //定时器
				if (startTime <= nowTime) {
					this.orderState = false;
					return
				}
				this.startTimer(spareTime);
			},
			startTimer(spareTime) {
				if (this.timer != null) this.timer = null;
				this.timer = setInterval(() => {
					spareTime--
					if (spareTime <= 0) {
						clearInterval(this.timer);
					}
					this.formatTime(spareTime)
				}, 1000)
			},
			formatTime(num) {
				let d = parseInt(num / (3600*24));
				let s = num - 3600*24*d;
				let h = parseInt(s/3600);
				s = s - 3600*h;
				let m = parseInt(s/60);
				s = s - 60*m;
				this.day = d ;
				this.hour = h ;
				this.minute = m;
				this.second = parseInt(s);
				if (this.hour == 0 && this.minute == 0&& this.day == 0 &&this.second == 0) {
					this.orderState = false;
				}
			},
			// 延长收货
			extendedReceipt() {
				this.$api.orderExtend({
					order_id:this.getOrderData.order_id
				}).then(res=>{
					uni.showToast({
						title: res.data.msg,
						duration: 1000,
						icon: "none"
					});
					if(res.data.errno == 0){
						this.getOrderData.is_extend = 2;
						clearInterval(this.timer);
						this.countDown(res.data.data.countdown_at);
						console.log(res)
					}
				})
			},
			// 去退款页面
			toRefund() {
				this.setReturnOrder({
					order_id: this.getOrderData.order_id,
					goods: this.getOrderData.factory[0].goods
				})
				uni.navigateTo({
					url: "/pages/order/refundReturn"
				})
			},
			// 打开是否确认取消订单弹窗
			isCancel() {
				this.$refs["remaks"].open(); //
			},
			// 关闭弹窗
			close(name) {
				this.$refs[name].close();
			},
			// 取消订单
			cancelOrder() {
				this.$refs['remaks'].close();
				uni.showLoading({
					title: '请稍后'
				})
				this.$api.orderCancel({
					order_id: this.getOrderData.order_id
				}).then(res => {
					if (res.data.errno == 0) {
						uni.hideLoading();
						uni.showToast({
							title: '订单取消成功',
							duration: 1000,
							icon: "none"
						});
						setTimeout(res => {
							uni.switchTab({
								url: "/pages/order/order"
							})
						}, 1000)
					}
				})

			},
			// 获取默认收货地址
			getDefaultAddress() {
				// 判断是否是立即下单页面 0 标识立即下单页面，需要去调用默认收货地址接口获取数据
				if (this.getOrederState != 0) {
					return
				}
				this.$api.addressConfirm().then(res => {
					if (res.data.errno == 0) {
					    console.log(res);
						this.setAddressData(res.data.data)
					}else {
                        this.setAddressData({})
					}
				})
			},
			//显示优惠券面板
			// toggleMask(type) {
			// 	let timer = type === 'show' ? 10 : 300;
			// 	let state = type === 'show' ? 1 : 0;
			// 	this.maskState = 2;
			// 	setTimeout(() => {
			// 		this.maskState = state;
			// 	}, timer)
			// },
			numberChange(data) {
				this.number = data.number;
			},
			changePayType(type) {
				this.payType = type;
			},
			// 生成订单
			createOrder() {
				console.log(this.getOrderData);
				let data = this.getOrderData;
				let data1 = [];
				data.factory.map((item, index) => {
					item.goods.map((v, i) => {
						data1.push({
							goods_id: v.goods_id,
							num: v.num,
							standard_id: v.standard_id
						})
					})
				})
				this.$api.orderCreate({
					address_id: this.getAddressData.address_id,
					commodity: data1
				}).then(res => {
					if (res.data.errno == 0) {
						console.log(res)
						let data = res.data.data
						this.payOrder(data);
					}
				})
			},
			// 订单支付
			payOrder(item) {
				let code = "";
				let data = {};
				// 判断是pay_number 还是order_id
				if (this.getOrederState == 0) {
					data = {
						pay_number: item.pay_number,
						js_code: "",
					}
				} else {
					data = {
						order_id: this.getOrderData.order_id,
						js_code: "",
					}
				}
				uni.login({
					provider: 'weixin',
					success: (loginRes) => {
						console.log(loginRes);
						if (loginRes.errMsg == "login:ok") {
							data.js_code = loginRes.code;
							this.$api.orderPay(data).then(res => {
								if (res.data.errno == 0) {
									console.log(res);
									let data = res.data.data;
									uni.requestPayment({
										provider: 'wxpay',
										timeStamp: data.timeStamp,
										nonceStr: data.nonceStr,
										package: data.package,
										signType: data.signType,
										paySign: data.paySign,
										success: function(res) {
											console.log('success:' + JSON.stringify(res));
											uni.showToast({
												title: '支付成功',
												duration: 1500,
												icon: "none"
											});
											uni.navigateTo({
												url: "/pages/index/paySuccess/paySuccess"
											})
										},
										fail: function(err) {
											console.log('fail:' + JSON.stringify(err));
											uni.showToast({
												title: '支付失败',
												duration: 1500,
												icon: "none"
											});
											uni.navigateTo({
												url: "/pages/index/payFail/payFail"
											})
										}
									});
								}
							})


						}
					}
				});

			},
			submit() {
				this.createOrder();
				// uni.redirectTo({
				// 	url: '/pages/index/paySuccess/paySuccess'
				// })
			},
			stopPrevent() {},
			// 跳转店铺主页
			toStoreHome() {
				console.log(this.getOrderData);
				uni.setStorageSync('factory_id', this.getOrderData.factory[0].factory_id)
				uni.navigateTo({
					url: "/pages/index/storeHome/storeHome"
				})
			},
			// 跳转地址选择列表
			toAddress() {
				if (this.getOrederState != 0) {
					return
				}
				this.setAddressState(1);
				uni.navigateTo({
					url: "/pages/mine/address/address"
				})
			}
		}
	}
